import React, { useEffect, useState } from 'react'
import { Chat } from '../components/Chat'
import { Sidebar } from '../components/Sidebar'
import { set, ref as ref_database, onValue } from "firebase/database";
import { db, auth } from "../firebase"
import { useNavigate } from "react-router-dom";
import { onAuthStateChanged } from "firebase/auth";


export const Home = () => {
  const navigate = useNavigate();
  const [user, setUser] = useState()
  const [userId, setUserId] = useState()
  const [userData, setUserData] = useState()
  const [userStatus, setUserStatus] = useState(true)
  const [side, setSide] = useState()
  const [sidebarDisplay, setsidebarDisplay] = useState(true)
  const [chatDisplay, setchatDisplay] = useState(true)


  useEffect(() => {
    onAuthStateChanged(auth, (user) => {
      if (user) {
        setUserId(user.uid);
        onValue(ref_database(db, 'users/' + user.uid), (snapshot) => {
          const data = snapshot.val();
          if (data) {
            navigate("/")
          }
          else {
            navigate("/details")
          }
        });

      } else {
        navigate("/login")
      }
    });


  }, [])

  useEffect(() => {
    onValue(ref_database(db, 'users/' + userId), (snapshot) => {
      const data = snapshot.val();
      if (data) {
        setUserData(data)
      }
    });
  }, [userId])


  useEffect(() => {
    if (userData) {
      if (userStatus) {
        set(ref_database(db, 'users/' + userId), {
          name: userData.name,
          userStatus: userData.userStatus,
          profile_picture: userData.profile_picture,
          status: "Online"
        });

      }
      else {

        const currentDate = new Date();
        const date = currentDate.getDate();
        const month = currentDate.getMonth();
        const year = currentDate.getFullYear();
        const minutes = currentDate.getMinutes();
        const hours = currentDate.getHours();
        const second = currentDate.getSeconds();
        const timestamp = new Date().getTime();
        // console.log(timestamp);

        let monthDateYear = date + '-' + (month + 1) + '-' + year;
        let time = hours + ':' + minutes;

        function convertTime(time) {
          let timeArr = time.split(':');
          let hours = parseInt(timeArr[0]);
          let minutes = timeArr[1];
          let amPm = hours >= 12 ? 'PM' : 'AM';
          hours = hours % 12;
          hours = hours ? hours : 12;
          if(minutes<10){
            minutes="0"+minutes;
          }
          let convertedTime = `${hours}:${minutes} ${amPm}`;
          return convertedTime;
        }

        set(ref_database(db, 'users/' + userId), {
          name: userData.name,
          userStatus: userData.userStatus,
          profile_picture: userData.profile_picture,
          status: "last seen at " +convertTime(time)+" on "+monthDateYear
        });

      }
    }
  }, [[],userStatus])


  window.addEventListener("visibilitychange", (event) => {
    if (document.visibilityState === "visible") {
      setUserStatus(true)
    } else {
      setUserStatus(false)
    }
  });

  window.addEventListener('beforeunload', function (e) {
    setUserStatus(false)
  });

  const [theme, setTheme] = useState();

  useEffect(() => {
    const currentTheme = localStorage.getItem('Theme');
    if (currentTheme) {
      setTheme(currentTheme);
    }
  });
  useEffect(() => {
    const home = document.getElementById('home');
    if (theme === 'dark') {
      // console.log('dark')
      const home = document.getElementById("home");
      home.classList.add("dark-theme")
      home.classList.remove("light-theme")

    }
    else if (theme === 'light') {
      // console.log('light')
      home.classList.add('light-theme')
      home.classList.remove('dark-theme')

    }
  }, [theme])
  const takeTheme = (data) => {
    setTheme(data)
  }
  const takeUser = (data) => {
    setUser(data);
    if (data) {
      if (window.innerWidth <= 850) {
        setsidebarDisplay(false)
        setchatDisplay(true)
      }
    }

  }
  const takeDisplay = (data) => {
    if (data) {
      setSide(Math.random())
      if (data) {
        // console.log(props.display);
        if (window.innerWidth <= 850) {
          setchatDisplay(false)
          setsidebarDisplay(true)
        }
      }
    }
  }

  return (
    <div id="home" className='light-theme home'>
      <div className="homeContainer">
        {sidebarDisplay && <Sidebar mode={takeTheme} userId={takeUser} display={side} />}
        {chatDisplay && <Chat userId={user} display={takeDisplay} />}

      </div>
    </div>
  )
}

