import React, { useState, useEffect } from 'react'
import logo from '../images/logo.png'
import google from '../images/google.png'
import loginLogo from '../images/login-illustration.gif'
import "./style.scss"
import { signInWithPopup, GoogleAuthProvider, onAuthStateChanged } from "firebase/auth";
import { auth } from "../firebase"
import { useNavigate } from "react-router-dom";
import { onValue, ref } from "firebase/database";
import { db } from "../firebase"



export const Login = () => {
    const provider = new GoogleAuthProvider();
    const navigate = useNavigate();
    const [err, setErr] = useState(false);
    const [login, setLogin] = useState(false);

 
    useEffect(() => {

        onAuthStateChanged(auth, (user) => {
          if (user) {
            onValue(ref(db, 'users/' + user.uid), (snapshot) => {
              const data = snapshot.val();
              if (data) {
                navigate("/")
              }
              else {
                navigate("/details")
              }
            });
    
          } else {
            setLogin(true)
          }
        });
      }, [])
    

    const signIn = async () => {

        try {
            await signInWithPopup(auth, provider)
                .then((result) => {
                    const user = result.user;
                    onValue(ref(db, 'users/' + user.uid), (snapshot) => {
                        const data = snapshot.val();
                        if (data) {
                            navigate("/")
                        }
                        else {
                            navigate("/details")
                        }
                    });

                })

        } catch (err) {
            setErr(true);
        }
    }


    if (login) {
        return (
            <div className='login'>
                <div className="loginContainer">
                    <div className="appName">
                        <img className="logo" src={logo} alt="logo" />
                        <h2>Chatcord</h2>
                    </div>
                    <img src={loginLogo} alt="login" />
                    <div className="welcome">
                        <h2 className='welcomeText'>Welcome to Chatcord</h2>
                        <p>Chat with your friends online</p>
                        <button onClick={signIn}>Continue with google <img src={google} alt="google logo" /></button>
                        {err && <span>Something went wrong</span>}
                    </div>
                </div>
            </div>



        )
    }

}

