import React, { useContext, useState, useEffect, useRef } from 'react'
import "./style.scss"
import logo from '../images/logo.png'
import addAvatar from '../images/AddAvatar.png'
import PersonIcon from '@mui/icons-material/Person';
import AutoAwesomeMosaicIcon from '@mui/icons-material/AutoAwesomeMosaic';
import { AuthContext } from '../context/AuthContext';
import { db, storage, auth } from "../firebase"
import { uploadBytesResumable, ref as ref_storage, getDownloadURL } from "firebase/storage"
import { set, ref as ref_database, onValue } from "firebase/database";
import { onAuthStateChanged } from "firebase/auth";
import { useNavigate } from "react-router-dom";




export const Details = () => {
  const [err, setErr] = useState(false);
  const [loading, setLoading] = useState(false);
  const [fill, setFill] = useState(false);
  const [details, setDetails] = useState(false);
  const { currentUser } = useContext(AuthContext)
  const navigate = useNavigate();

  useEffect(() => {
    onAuthStateChanged(auth, (user) => {
      if (user) {
        onValue(ref_database(db, 'users/' + user.uid), (snapshot) => {
          const data = snapshot.val();
          if (data) {
            navigate("/")
          }
          else {
            setDetails(true)
          }
        });

      } else {
        navigate("/login")
      }
    });
  }, [])


  const storageRef = ref_storage(storage, `${currentUser.uid}`);

  onValue(ref_database(db, 'users/' + currentUser.uid), (snapshot) => {
    const data = snapshot.val();
    if (data) {
      navigate("/")
    }
  });



  const selectImage = () => {
    const image = document.getElementById("profileImage");
    
    image.addEventListener("change", () => {
      const files = document.getElementById("profileImage").files[0];
      if (files) {
        const imgPreview = document.getElementById("imgpreview");
        const fileReader = new FileReader();
        fileReader.readAsDataURL(files);
        fileReader.addEventListener("load", function () {
          imgPreview.style.display = "block";
          imgPreview.innerHTML = '<img src="' + this.result + '" />';

        });
      }
    })
  }


  const handleSubmit = async (e) => {
    e.preventDefault()
    const displayName = e.target[0].value;
    const userStatus = e.target[1].value;
    const userImage = e.target[2].files[0];
    if (displayName !== "") {
      if (userStatus !== "") {
        if (userImage !== undefined) {
          try {
            setLoading(true);
            setFill(false)
            await uploadBytesResumable(storageRef, userImage).then(() => {
              setLoading(true);
              getDownloadURL(storageRef).then(async (url) => {
                console.log(url)
                await set(ref_database(db, 'users/' + currentUser.uid), {
                  name: displayName,
                  userStatus: userStatus,
                  profile_picture: url,
                  status: "Online"
                });
                navigate("/")
              })
            })

          } catch (error) {
            setErr(true);
            setLoading(false);
          }


        }
        else {
          setFill(true)
        }
      }
      else {
        setFill(true)
      }

    }
    else {
      setFill(true);
    }





  }
  if (details) {
    return (



      <div className="details">
        <form className="detailsContainer" onSubmit={handleSubmit}>
          <div className="appName">
            <img className="logo" src={logo} alt="logo" />
            <h2>Chatcord</h2>
          </div>
          <h2>Create your profile</h2>
          <div className='input'><PersonIcon style={{ fontSize: "30px" }} /><input type="text" placeholder='Name' /></div>
          <div className='input'><AutoAwesomeMosaicIcon style={{ fontSize: "30px" }} /><input type="text" placeholder='Write about yourself ' /></div>

          <input onClick={selectImage} style={{ display: "none" }} type="file" id="profileImage" accept="image/png, image/gif, image/jpeg" />
          <label htmlFor="profileImage"><img src={addAvatar} alt="avatar" /><span>Add an avatar</span></label>
          <div id='imgpreview'></div>
          {loading && <span>Uploading and compressing the image please wait...</span>}
          {err && <span>Something went wrong</span>}
          {fill && <span>Please fill all details</span>}
          <button type='submit'>Continue</button>
        </form>
      </div>
    )
  }
}
